import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { login } from './features/counter/userSlice';
import { auth } from './firebase';
import './Login.css';

function Login() {

    const [email,setEmail] = useState("");
    const [password,setPassword] = useState("");
    const [name,setName] = useState("");
    const [profilePicture,setProfilePicture] = useState("");
    const dispatch = useDispatch();
    

    const register = () => {
        if(!name) {
            return alert("Please enter your full name!");
        }

        auth.createUserWithEmailAndPassword(email, password)
        .then((userAuth) => {
            userAuth.user.updateProfile({
                displayName: name,
                photoURL : profilePicture,
            })
            .then(() => {
                dispatch(login({
                    email: userAuth.user.email,
                    uid: userAuth.user.uid,
                    displayName: name,
                    photoUrl: profilePicture,
                }));
            });
        }).catch((error) => alert(error));
    };

    const loginToApp = (e) => {
        e.preventDefault();

        auth.signInWithEmailAndPassword(email, password,)
        .then((userAuth) => {
            dispatch(login({
                email: userAuth.user.email,
                uid: userAuth.user.uid,
                displayName: userAuth.user.displayName,
                photoUrl: userAuth.user.photoURL,
            }));
        }).catch((error) => alert(error));
    };

    return (
        <div className="login">
            <img className="Login__logoImage" src="data:image/png;base64," alt="Logo" />
            <h2 className="login__appName">NetworKit</h2>

            <form>
                <input value={name} onChange={(e) => setName(e.target.value)} placeholder="Full Name (if registering)" type="text" />
                <input value={profilePicture} onChange={(e) => setProfilePicture(e.target.value)} placeholdertype="text" placeholder="Profile pic URL (optional)" />
                <input value={email} onChange={(e) => setEmail(e.target.value)} placeholder="Email" type="email" />
                <input value={password} onChange={(e) => setPassword(e.target.value)}placeholder="Password" type="password" />

                <button className="login__signInButton" type="submit" onClick={loginToApp}>Sign In</button>
            </form>

            <p>Not a member? {" "}
            <span onClick={register} className="login__register">Register Now!</span>
            </p>
        </div>
    )
}

export default Login
